html {
    scroll-behavior: smooth;
}

.gotopbtn {
    position: fixed;
    /* width: 40px; */
    /* height: 40px; */
    /* background: rgb(16, 25, 39); */
    /* bottom: 40px; */
    /* right: 50px; */

    text-decoration: none;
    text-align: center;
    line-height: 40px;
    color: #1A3557;
    font-size: 40px;
    border-radius: 5px;
    z-index: 2;
    opacity: 0.4;
    transition: 0.5s;
}

.gotopbtn:hover {
    cursor: pointer;
    opacity: 1;
}